.Toolbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    position: fixed;
    padding: 5px 15%;
    height: 50px;
    background-color: rgba(6, 43, 31, 0.911);
    z-index: 100;
}

@media(max-width:500px) {
    .Logo,
    .Categories {
        display: none;
    }
}


