.PostHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 55px;
}

.Avatar {
    border-radius: 50%;
    overflow: hidden;
    height: 60px;
    width: 60px;
}

.userName {
    font-size: 25px;
    text-transform: capitalize;
    position: relative;
    top: -30px;
    left: 10px;
}

.Category {
    padding: 5px 15px;
    background-color: rgb(82, 82, 230);
    border-radius: 20px;
    color: white;
    font-size: 20px;
    height: 40px;
    -ms-flex-item-align: center;
        align-self: center;
}

@media(max-width:750px) {
    .PostHeader {
        -ms-flex-direction: column;
            flex-direction: column;
        margin-bottom: 30px;
    }
    .Category {
        margin-top: 10px;
    }
}


