.PostConstructorHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 75%;
}

.PostConstructorHeader input {
    text-indent: 10px;
    height: 50px!important;
    border-bottom: 1px solid grey;
    position: relative;
    top: 6px;
}

.PostConstructorHeader input:focus {
    border-bottom: 1px solid grey;
}

.PostConstructorHeader button {
    width: 100px;
    height: 50px;
    padding: 0 10px;
    margin: 0 5px 5px 0;
    -ms-flex-item-align: center;
        align-self: center;
    border: none;
}

.PostConstructorHeader button:disabled {
    border: none;
    color: grey;
    cursor: not-allowed;
}


